import Preloader from "../layout/preoladers/Preloader";
import React from "react";
import ProfileStatusWithHook from "./ProfileStatusWithHook";

export const Profile = (props) => {
    if (!props.profile) {
        return <Preloader/>
    }
    console.log(props)
    return (
        <div className="profile__wrap">
            <div className="profile__top">
                {props.profile.fullName}
            </div>
           <div className="profile__item">
               <div className="profile__avatar">
                   <img src={props.profile.photos.small} alt=""/>
               </div>

           </div>
            <div className='profile__info'>
                <div className='profile__name'>
                    {props.profile.fullName}
                </div>
                <ProfileStatusWithHook  status = {props.status} updateUserStatus = {props.updateUserStatus}/>
                <div className='profile__about'>
                    <div>
                        О себе:
                    </div>
                    <div>
                        {props.profile.aboutMe}
                    </div>

                </div>
                <div className='profile__links'>
                    <div>Ссылки:</div>
                    <div className='profile__link'>
                        <div className='profile__link-name'>Instagram: </div>
                        <div className='profile__link-item'>{props.profile.contacts.instagram}</div>
                    </div>
                    <div className='profile__link'>
                        <div className='profile__link-name'>twitter: </div>
                        <div className='profile__link-item'>{props.profile.contacts.twitter}</div>
                    </div>
                    <div className='profile__link'>
                        <div className='profile__link-name'>Вконтакте: </div>
                        <div className='profile__link-item'>{props.profile.contacts.vk}</div>
                    </div>
                </div>
            </div>
        </div>
    )
}